<template>
  <div class="container my-5 px-4 px-lg-5">
    <div class="row gx-4 gx-lg-5 justify-content-center">
      <div class="col-md-10 col-lg-8 col-xl-7">
        <h1></h1>
        <div class="my-5">
          <form id="userform" method="post" action="">
            <div class="text-start">
              <label for="name"></label>
              <input
                class="form-control"
                id="name"
                type="text"
                v-model="name"
                placeholder=" ."
              />
            </div>
            <div class="text-start">
              <label for="id">ID</label>
              <input
                class="form-control"
                id="id"
                type="text"
                v-model="id"
                placeholder="ID ."
              />
              <div id="idresult" class="mt-1"></div>
            </div>
            <div class="text-start">
              <label for="pass">Password</label>
              <input
                class="form-control"
                id="pass"
                type="password"
                v-model="pass"
                placeholder=" ."
              />
            </div>
            <div class="text-start">
              <label for="pass">Password Check</label>
              <input
                class="form-control"
                id="passcheck"
                type="password"
                v-model="passcheck"
                placeholder="  ."
              />
            </div>
            <!-- email  -->
            <div class="text-start">
              <p class="mt-3 mb-0">()</p>
              <label for="email">E-mail</label>
              <input
                class="form-control"
                id="email"
                type="e-mail"
                v-model="email"
                placeholder="e-mail .(example: -----@-----.---  )"
              />
            </div>
            <!--  -->
            <div class="text-start">
              <label for="genre"> </label>
              <input
                class="form-control"
                id="genre"
                type="text"
                v-model="genre"
                placeholder="  ."
              />
            </div>
            <!--  -->
             <div class="text-start">
              <label for="goal"></label>
              <input
                class="form-control"
                id="goal"
                type="text"
                v-model="goal"
                placeholder=" ."
              />
            </div>           
            <!--   -->
             <div class="text-start">
              <label for="socialLink">SNS </label>
              <input
                class="form-control"
                id="socialLink"
                type="url"
                v-model="socialLink"
                placeholder=" .(example: https://----.---  )"
              />
            </div>    
            <br />
            <!-- Submit Button-->
            <b-button variant="primary" @click="checkValue"
              >Sign Up</b-button
            >&nbsp;
            <b-button variant="danger" @click="resetValue"
              >Cancel</b-button
            >
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/config/http-common.js";

export default {
  name: "Signup",
  data() {
    return {
      name: "",
      id: "",
      pass: "",
      passcheck: "",
      // email: "",
      // genre: null,
      // goal: null,
      // socialLink: ""
    };
  },

  methods: {
    //   -   registBook 
    checkValue() {
      //   
      // isbn, , , ,        
      let err = true;
      let msg = "";
      !this.name &&
        ((msg = " ."),
        (err = false)) /*, this.$refs.name.focus()*/;
      err && !this.id && ((msg = "ID ."), (err = false)) /*,
        this.$refs.id.focus()*/;
      err &&
        !this.pass &&
        ((msg = " ."), (err = false)) /*,
        this.$refs.id.focus()*/;
      err &&
        this.pass != this.passcheck &&
        ((msg = "  "), (err = false));
      if (!err) alert(msg);
      else this.registMember();
    },  

    //      
    createMarathon(id) {
      http
        .post(`/marathon/create`, {
          userId: id,
          goalPages: 50 //  50 
        })
        .then(({ data }) => {
          //      .
          console.log(data)
        })
        .catch((err) => {
          console.log(err.response)
          alert("  .  .")
        })
    },
    registMember() {
      http
        .post(`/users`, {
          userName: this.name,
          userId: this.id,
          password: this.pass,
          email: this.email,
          genre: this.genre,
          goal: this.goal,
          socialLink: this.socialLink
        })
        .then(({ data }) => {
          let msg = "  .";
          //      .
          if (data === "success") {
            msg = " .";
            //  
            alert(msg);
            this.$router.push({ name: "Home" });
          }
        })
        .catch((err) => {
          if (err.response.data === "   ID.") {
            alert(err.response.data)
          }
        })
    },
    resetValue() {
      this.$router.push("/");
    },
  },
};
</script>

<style></style>
