<template>
  <div>
    <b-button
      v-b-modal.modal3-prevent-closing
      variant="primary"
      class="col-4 mb-3"
      @click="$bvModal.show('modal-scoped')"
      > </b-button
    >
    <b-modal scrollable id="modal3-prevent-closing" ref="modal" title=" ">
      <form ref="form">
        <b-form-group
          class="roommodal"
          id="dropdown-1"
          label=""
          label-for="usage-roominput"
        >
          <b-form-select v-model="selectedOption" :options="options">
            <template #first>
              <b-form-select-option :value="null" disabled
                >-- --</b-form-select-option
              >
            </template>
          </b-form-select>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          class="roommodal"
          label=" "
          label-for="title"
        >
          <b-form-input
            id="title"
            v-model="roomName"
            placeholder="  "
          ></b-form-input>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          type="textarea"
          class="roommodal"
          label=""
          label-for="description"
        >
          <b-form-textarea
            id="description"
            v-model="roomContent"
            placeholder=" "
            row="3"
            max-rows="6"
          ></b-form-textarea>
        </b-form-group>
      </form>
      <form ref="form" class="">
        <b-form-group
          type="search"
          class="searchmodal"
          label=" "
          label-for="bookSearch"
        >
          <b-form-input
            id="bookSearch"
            placeholder=" "
            v-model="bookSearchValue"
            plain
            @keydown.enter.prevent="bookSearch()"
          >
          </b-form-input>
          <b-button class="offset-10" @click="bookSearch()">search</b-button>
        </b-form-group>
      </form>
      <div v-if="bookData===1">
        <p>  .</p>
      </div>
      <div v-else-if="bookData===2">
        <p> : {{ selectedBook }}    <span class="text-decoration-underline" @click="bookData=1">[ ]</span></p>
      </div>
      <div v-else-if="bookData.length===0">
        <p> .</p>
      </div>
      <div v-else>
        <!-- {{ bookData }} -->
        <b-list-group>
          <b-list-group-item v-for="book in bookData" :key=book.isbn>
            {{ transStr(book.title) }}    
            <a :href="book.link" class="text-reset" onclick="window.open(this.href, '_blank', 'width=800, height=600'); return false;">[]</a>
            <span class="text-decoration-underline" @click="selectBook(book)">[]</span>
          </b-list-group-item>
        </b-list-group>
      </div>


      <br />
      <template #modal-footer="{ ok, cancel }">
        <b-button
          variant="outline-primary"
          class="me-2"
          @click="roomCreate() ? ok() : _"
        >
          
        </b-button>
        <b-button
          @click="
            formReset();
            cancel();
          "
        >
          
        </b-button>
      </template>
    </b-modal>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Swal from "sweetalert2";
import http from "@/config/http-common.js";

export default {
  name: "ConferenceCreate",
  data() {
    return {
      roomName: "",
      roomContent: "",
      bookSearchValue: "",
      selectedOption: null, // ( )             
      options: [ //   DB        //      
        { value: "1", text: "" },
        { value: "2", text: "" },
        { value: "3", text: "" },
      ],
      bookData: 1,
      selectedBook: '',
      bookDetailId: null
    };
  },
  created() {},
  computed: {
    ...mapState("userStore", ["userInfo"]),
  },
  methods: {
    ...mapActions("conferenceStore", ["createRoom"]),
    roomIsValid: function () {
      if (this.selectedOption === null || this.roomName === "" || this.bookDetailId === null) {
        Swal.fire({
          icon: "error",
          title: "Stop!",
          text: ", ,   .",
        });
        return false;
      } else if (this.roomName.length > 31) {
        Swal.fire({
          icon: "error",
          title: "RoomnameError",
          text: "  30  .",
        });
        return false;
      } else {
        return true;
      }
    },
    roomData() {
      return {
        user: {
          userId: this.userInfo.userId, 
        },
        bookDetail: {
          id: this.bookDetailId, 
        }, //  - book DB  
        conferenceType: { 
          id: this.selectedOption,
        },
        title: this.roomName,
        description: this.roomContent,
        isActive: 1
      };
    },
    formReset() {
      this.selectedOption = null;
      this.thumbnailFile = "";
      this.roomName = "";
      this.roomContent = "";
      this.roomSearchValue = "";
      this.bookData = 1 ;
    }, //  form 
    roomCreate() {
      if (this.roomIsValid()) {
        this.createRoom(this.roomData()); //   //   payload 
        this.formReset(); //  form 
        return true;
      } else {
        return false;
      }
    },
    bookSearch() {
       http({
        method: "get",
        url: `/search/naver?query=${this.bookSearchValue}`,
      })
        .then((response) => {
          console.log(response);
          this.bookData = response.data.items
        })
        .catch((error) => {
          console.dir(error);
        });
    },
    transStr(str) {
      // return str
      var transedStr = str.replaceAll('<b>', '')
      transedStr = transedStr.replaceAll('</b>', '')
      return transedStr
    },
    // DB      book  create // post
    createBookDB(book) {
      http({
        method: "post",
        url: `/search/create`,
        data: {
          author: book.author,
          isbn: book.isbn,
          overview: book.description,
          price: book.price,
          publishDate: book.pubdate,
          publisher: book.publisher,
          title: book.title,
          titleUrl: book.image,
        }
      })
        .then((response) => {
          console.log(response);
          this.bookDetailId = response.data
        })
        .catch((error) => {
          console.dir(error);
        });
      return true
    },
    // DB     isbn check // get
    checkBookInDB(book) {
       const isbn = book.isbn.split(" ")[1]
       console.log(isbn)
       http({
        method: "get",
        url: `/search/${isbn}`,
      })
        .then((response) => {
          console.log(response);
          this.bookDetailId = response.data
        })
        .catch((error) => {
          console.dir(error);
          this.createBookDB(book)
        });
    },
    selectBook(book) {
      this.selectedBook = this.transStr(book.title)
      this.bookData = 2
      this.bookDetail.id = this.checkBookInDB(book)
    }
  },
};
</script>

