<template>
  <div class="col">
    <b-card no-body bg-variant="dark" class="overflow-hidden mx-2 my-2" style="max-width: 540px;">
      <b-row no-gutters>
        <b-col md="6">
          <b-card-img v-if="item.bookDetail.titleUrl" :src="item.bookDetail.titleUrl" alt="Image" class="rounded-3 mt-3 ms-3"></b-card-img>
          <b-card-img v-else :src="thumbnail_url" alt="Image" class="rounded-3 mt-3 ms-3"></b-card-img>
        </b-col>
        <b-col md="6" class="mb-3">
          <b-card-body :title="item.title"> <!-- -->
            <b-card-text>
              <p class="card-text">: {{ item.id }}</p> <!-- -->
              <p class="card-text">: {{ item.bookDetail.title }}</p> --> <!--bookStore    ..?-->
              <p class="card-text">: {{ item.conferenceType.name }}</p> 
              <p class="card-text">: {{ item.callStartTime }}</p>
              <!-- <p class="card-text">: {{ item.callEndTime }}</p> --> <!--  -->
              <p v-if="item.isActive" class="card-text"> : <span class="text-success"></span> </p> 
              <p v-else class="card-text"> : <span class="text-danger"></span></p> 
              <p class="card-text">  : {{ item.participant.length }}</p> <!--participant array  -->
              
            </b-card-text>
          </b-card-body>
          <b-button variant="primary" @click="enterConference" v-if="item.isActive"></b-button> <!-- id  detail  -->
          <b-button variant="secondary" class="disabled" v-else></b-button> <!-- id  detail  -->
        </b-col>
      </b-row>
    </b-card>
  </div>


</template>

<script>
import { mapState, mapActions } from "vuex";

const userStore = "userStore";
const conferenceStore = "conferenceStore";

export default {
  name: 'ConferenceListItem',
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapState(userStore, ["userInfo"]),
    ...mapState(conferenceStore, ["conferenceDetail"]),
  },
  methods: {
    ...mapActions("conferenceStore", ["createRoom", "getConferenceCategories"]),
    enterConference() {
      //   
      this.getConferenceCategories()
      this.$router.push({
        path: `conference/view/${this.item.id}`,
      });
    }
  },
  data: function () {
    return {
      thumbnail_url: require('@/assets/thumbnail/thumbnail_default_img.png') //  thumbnail ....
    }
  }
}
</script>

