<template>
  <div class="container">
    <!--    db   -->
    <vue-profile 
    :nickname="`${userInfo.userName}`"
    :bodyPhrase="` : ${userInfo.genre} & : ${userInfo.goal}`"
    :socialLinks="myLinks"
    :profileImg="Img"
    coverColor="#5498e9"
    /> 
    <!--   -->
    <!-- <b-button v-b-modal.modal2-prevent-closing class="me-3" @click="getUserData()"> </b-button> -->
      <b-modal
        id="modal2-prevent-closing"
        ref="modal"
        title=" "
        hide-footer
        v-if="modalCheck"
      > 
        <form ref="form">
          <b-form-group
            type="file"
            label="  "
            label-for="profileImg"
          >
            <b-form-file
              id="profileImg"
              accept=".png, .jpg, .jpeg, .gif"
              v-model="user.profileImage"
              plain
            ></b-form-file>
            <div class="mt-3">Selected file: {{ user.profileImage ? user.profileImage : '' }}</div>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label=" username"
            label-for="username"
          >
            <b-form-input
              id="username"
              v-model="user.user_name"
              placeholder=" username "
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label=" "
            label-for="user_pw"
          >
            <b-form-input
              type="password"
              id="user_pw"
              v-model="user.user_pw"
              placeholder=" password "
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="  "
            label-for="user_pw_check"
          >
            <b-form-input
              type="password"
              id="user_pw_check"
              v-model="user.user_pw_check"
              placeholder=" password "
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label=" email"
            label-for="user_email"
          >
            <b-form-input
              type="email"
              id="user_email"
              v-model="user.email"
              placeholder=" e-mail (-----@-----.--- )"
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="  "
            label-for="user_genre"
          >
            <b-form-input
              type="text"
              id="user_genre"
              v-model="user.genre"
              placeholder="   "
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="  "
            label-for="user_goal"
          >
            <b-form-input
              type="text"
              id="user_goal"
              v-model="user.goal"
              placeholder="  "
              @keyup.enter="userInfoUpdate()"
            ></b-form-input>
          </b-form-group>
        </form>
        <form>
          <b-form-group
            label=" SNS "
            label-for="user_socialLink"
          >
            <b-form-input
              type="url"
              id="user_socialLink"
              v-model="user.socialLink"
              placeholder=" SNS (https://----.--- )"
              @keyup.enter="userInfoUpdate()"
            ></b-form-input>
          </b-form-group>
        </form>


        <hr />
        <b-button @click="userInfoUpdate()">  </b-button>
      </b-modal>
    <b-button @click="deleteUser"></b-button>
    <!--    -->
    <b-row class="justify-content-center mt-4" v-if="marathon.value">  
      <p class="fs-5 fw-bold"><i class="fas fa-running text-primary"></i>  <i class="fas fa-running text-primary"></i></p> <!--   -->
      <b-progress
        :max="marathon.max"
        show-value
        class="mb-3 col-4 px-0"
        animated
        height="2rem"
      >
        <b-progress-bar :value="marathon.value">
          <span><strong>{{ marathon.value }} / {{ marathon.max }}</strong></span>
        </b-progress-bar>
      </b-progress>
      <p v-if="marathon.value > 0 && marathon.value===marathon.max"> 100% !  .<b-button size="sm" @click="updateGoal()"></b-button></p>
      <p v-else> {{ marathon.value / marathon.max * 100  }}% !</p>
    </b-row>
    <div class="space"></div>

  </div>
  
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";
import { mapActions } from "vuex";
import http from "@/config/http-common.js";


const userStore = "userStore";
export default {
  data() {
    return {
      myLinks: {         
        email: "",
        link: "", 
      },
      Img: 'https://dcimg4.dcinside.co.kr/viewimage.php?id=26b4ca33ebd339af&no=24b0d769e1d32ca73ceb84fa11d02831abf8dd37dbca42c9c56298cca8560265a7d8652086bfe4b9343f67919400852e8de3921a268bec3508394fa2d71e05d88e6ff22c1eb5a3174ddf19d920114166da3bce0caad0a7b3d2c982290a031781',
        //  (  )   
      marathon: {
        value: 0, //        
        max: 10, // max value        
      },
      user: {
        profileImage: "",
        user_name: "",
        user_pw: "",
        user_pw_check: "",
        email: "",
        genre: "",
        goal: "",
        socialLink: "",
      },
      modalCheck: false,
    };
  },
  methods: {
    ...mapMutations(userStore, ["SET_IS_LOGIN", "SET_USER_INFO"]),
    ...mapActions(userStore, ["getUserInfo"]),
    //  
    deleteUser() {
      // 
      if (confirm('?')) {
        http({
        method: 'delete',
        url: `/users/${this.userInfo.userId}`,
        })
        .then((response) => {
          console.log(response);
          // 
          this.SET_IS_LOGIN(false);
          this.SET_USER_INFO(null);
          this.$router.push({
            path: "/",
          })
          alert(' ')
        })
        .catch((err) => {
          console.dir(err);
        })
      } else {
        alert(' ')
      }
    },
    getUserData() { //   
      this.modalCheck = true
      this.user.user_name = this.userInfo.userName
      this.user.email = this.userInfo.email
      this.user.genre = this.userInfo.genre
      this.user.goal = this.userInfo.goal
      this.user.socialLink = this.userInfo.socialLink
      this.user.profileImage = this.userInfo.profileImage
      
    },
    checkInfo() { //   
      console.log(this.user.profileImage)
      if(this.user.user_pw != this.user.user_pw_check) {
        alert('  ')
        return false
      // } else if(this.user.profileImage === null) {
      //   alert('  ')
      //   return false
      } else if(this.user.user_pw === '' && this.user.user_pw_check == '') {
        alert(' password ')
        return false
      } else if(this.user.user_name === '') { //   
        alert(' username ')
        return false
      } else if(this.user.user_name === this.userInfo.userName ) {
        if (confirm('username  ?')) {
          return true
        } else {
          return false
        }
      } else {
        return true
      }

    },
    userInfoUpdate() { //  
      if(this.checkInfo()) {
        http({
          headers: { 'Content-Type': 'multipart/form-data' }, //  
          method: 'put',
          url: `/users/${this.userInfo.userId}`,
          data: {
            'password': this.user.user_pw,
            'userId': this.userInfo.userId,
            'userName': this.user.user_name,
            'email': this.user.email,
            'genre': this.user.genre,
            'goal': this.user.goal,
            'socialLink': this.user.socialLink,
            'profileImage': this.user.profileImage //  
            }
          })
          .then((response) => {
            console.log(response)
            alert('  ')

            //         ( userStore )
            let token = sessionStorage.getItem("access-token");
            this.getUserInfo(token)
            this.myLinks.email = this.userInfo.email, //  
            this.myLinks.link = this.userInfo.socialLink //  
           
            //       
            this.user.user_pw = ""
            this.user.user_pw_check = ""
            this.modalCheck = false
            
          })
          .catch((err) => {
            console.dir(err)
          })
      }
    },
    //  
    getMarthonInfo() {
      http({
        method: "get",
        url: `/marathon/marathon-info/${this.userInfo.userId}`,
      })
        .then((response) => {
          console.log(response.data)
          this.marathon.value = response.data.nowPages
          this.marathon.max = response.data.goalPages
          
        })
        .catch((error) => {
          console.log(error.response)
          alert('    .  ')
        });
    },
    //  (goalPages)  -->   
    updateGoal(){
       http({
        method: "put",
        url: `/marathon/${this}`,
      })
        .then((response) => {
          console.log(response)
        })
        .catch((error) => {
          console.log(error)
        });
    }
  },
  created() {
    //  
    this.myLinks.email = this.userInfo.email,
    this.myLinks.link = this.userInfo.socialLink
    //    
    this.getMarthonInfo()
    
  },
  computed: {
    ...mapState(userStore, ["userInfo"]),
  },
};
</script>

<style>
.space {
  height: 250px;
}
</style>
